@import "../../styles/variables";
.sidebar {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
  flex-grow: inherit;
  margin-top: 30px;
  width: 250px;
  height: 500px;
  border-radius: 20px;
  //background-color: #424842dd;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  .cart {
    margin: 0px;
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .shopping-cart-sidebar {
      width: 100px;
      height: 100px;
    }
  }
  .items {
    padding-left: 5px;
    padding-right: 5px;
    overflow-y: scroll;
    max-height: 250px;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
  }

  .order {
    display: flex;

    justify-content: space-between;

    .orderbtn {
      height: 50px;
      width: 100px;
      background-color: $color2;
      border: none;
      border-radius: 20px;
      outline: none;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      &:active {
        box-shadow: none;
      }
    }
    .clear {
      border: none;
      border-radius: 20px;
      height: 50px;
      width: 100px;
      background-color: $color2;
      outline: none;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      &:active {
        box-shadow: none;
      }
    }
  }
}
